.Order {
    width: 80%;
    border: 1px solid #eeeeee;
    -webkit-box-shadow: 0 2px 3px #ccc;
            box-shadow: 0 2px 3px #ccc;
    padding: 15px;
    margin: 10px auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-bottom: 30px;
}

.IngredientsList {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.IngsContainer {
    margin-top: 15px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.Ing {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 5px 10px;
    border: 1px solid #ccc;
    margin-right: 15px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.IngredientsList div img{
    height: 20px;
}

@media (max-width: 715px) {
    .IngsContainer{
        -ms-flex-direction: column;
            flex-direction: column;
        margin-right: 0;
    }
    .Ing {
        margin-right: 0;
        margin-top: 15px;
    }
    .Ing:first-child {
        margin-top: 0;
    }
}


